  removeEntry(_id){
    check(_id, String);
    if(confirm('Remove configuration?')){
        console.log('removing...', _id)

        //ServiceConfiguration.configurations.remove({_id: _id});
        Meteor.call('removeService', _id, function(error, result){
            console.log('removeService[error]', error)
            console.log('removeService[result]', result)
        })
    }
  }
