/**
 * Get the user access token if it exists.
 * @returns {*}
 */
function getUserAccessToken() {
    var user = Meteor.user();

    if (!get(user, 'services.OAuth2Server')) {
        return;
    }

    return user.services.OAuth2Server.accessToken;
}


