import { get } from 'lodash';
import { OAuth } from 'meteor/clinical:oauth';
import { Session } from 'meteor/session';

Session.setDefault('ehrLaunchContext', '');
export default OAuth.requestCredential = function (options, credentialRequestCompleteCallback, serviceName) {
  console.log("********************************************")
  console.log('Yes, smart-on-fhir-client/client/OAuth.js is being run...')

  if(get(Meteor, 'settings.public.logging') === "debug"){
    console.log('OAuth.requestCredential ', options);
  }  
  if (!credentialRequestCompleteCallback && typeof options === 'function') {
    credentialRequestCompleteCallback = options;
    options = {};
  }

  if(get(Meteor, 'settings.public.logging') === "debug"){
    console.log('OAuth.requestCredential().OAuth2', OAuth2);
  }  

  if(!serviceName){
    serviceName = OAuth2.serviceName
  }

  if(get(Meteor, 'settings.public.logging') === "debug"){
    console.log('OAuth.requestCredential().serviceName', serviceName);
  }  

  var config = ServiceConfiguration.configurations.findOne({
    service: serviceName
  });
  if(get(Meteor, 'settings.public.logging') === "debug"){
    console.log('Fetched the service configuration...', config);
  }  

  if (!config) {
    credentialRequestCompleteCallback
      && credentialRequestCompleteCallback(
        ServiceConfiguration.ConfigError('Service not configured')
      );
  } else {
    if (!config.baseUrl) {
        credentialRequestCompleteCallback
          && credentialRequestCompleteCallback(
            ServiceConfiguration.ConfigError(
              'Service found but it does not have a baseUrl configured.')
          );
      }
    
      if (!config.loginUrl) {
        credentialRequestCompleteCallback
          && credentialRequestCompleteCallback(
            ServiceConfiguration.ConfigError(
              'Service found but it does not have a loginUrl configured.')
          );
      }
    
      if(get(Meteor, 'settings.public.logging') === "debug"){
        console.log(serviceName + ' found and is configured.  Creating credentialToken and scopes.');
      }  
      var credentialToken = Random.secret();
      //var credentialToken = '2e80e475-5930-48f3-968e-de4429f09572';
    
      // always need this to get user id from service.
      // TODO: if(serviceName === "Epic")
      var requiredScope = [
        'email', 
        'OBSERVATION.READ', 
        'OBSERVATION.SEARCH', 
        'PATIENT.READ', 
        'PATIENT.SEARCH', 
        'PRACTITIONER.READ', 
        'PRACTITIONER.SEARCH',
        'patient/*.read',
        'patient/*.search',
        'openid'
      ];
      var scope = [
        'OBSERVATION.READ', 
        'OBSERVATION.SEARCH', 
        'PATIENT.READ', 
        'PATIENT.SEARCH', 
        'PRACTITIONER.READ', 
        'PRACTITIONER.SEARCH',
        'patient/*.read',
        'patient/*.search',
        'openid',
        'email',
        'profile',
        'user/*.*',
        'launch',
        'online_access'
      ];
      if (options.scope) {
        scope = options.scope;
      }
      
      scope = _.union(scope, requiredScope);
    
      if(get(Meteor, 'settings.public.logging') === "debug"){
        console.log('scope', scope);
      }  

      var loginStyle = OAuth._loginStyle(serviceName, config, options);
    
      var loginUrl = config.loginUrl +
        '?response_type=code' +
        '&client_id=' + config.clientId +
        '&redirect_uri=' + OAuth._redirectUri(serviceName, config) +
        '&state=' + OAuth._stateParam(loginStyle, credentialToken);

      if(get(Meteor, 'settings.public.logging') === "debug"){
        console.log('Creating specific loginUrls for popular EHRs...', serviceName);
      }  

        
      if(serviceName === "iHealthLocal"){
        loginUrl = loginUrl + '&APIName=OpenApiBP';
      }
        
      // apparently some OAuth servers want a client to present scope and state at login
      // could be Google, Facebook, Twitter, etc.  
      // this code was present in the original oauth library
      // however, as we implement Epic specific connectivity, we don't need it
      // but we probablly want to keep this around for non-epic systems
      // so parking it for now 

      if(serviceName === "Omnibus"){
        console.log('Detected the Omnibus service...')
        loginUrl = loginUrl + '&aud=patient%2FPatient%2*'  // ocp-fis; user/*; patient/Patient
                            + '&launch=' + Session.get('ehrLaunchContext')
                            + '&scope=user%2FPatient.read%20'
                                   + 'user%2FEncounter.read%20'
                                   + 'user%2FPerson.read%20'
                                   + 'patient%2FPractitioner.read%20'
                                   + 'launch%2Fpatient%20'
                                   + 'online_access%20'
                                   + 'openid%20'
                                   + 'profile%20'
                                   + 'user%2FPatient.read%20'
                                   + 'patient%2FDocumentReference.read%20'
                                   + 'user%2FObservation.write%20'
                                   + 'lauch%2Forganization%20'
                                   + 'user%2FObservation.read%20'
                                   + 'patient%2FPatient.read%20'
                                   + 'user%2FDocumentReference%20'
                                   + 'patient%2FPerson%20'
                                   + 'patient%2FObservation.read%20'
                                   + 'patient%2FEncounter.read%20'
                                   + 'patient%2FDocumentReference.write%20'
                                   + 'user%2FDocumentReference.read%20'
                                   + 'patient%2FObservation.write%20'

      }
      if(serviceName === "CernerPatient"){
        console.log('Detected the CernerPatient service...')
        loginUrl = loginUrl + '&aud=' + config.baseUrl 
                            + '&scope=user%2FPatient.read%20'
                              + 'user%2FAllergyIntolerance.read%20'
                              + 'user%2FCarePlan.read%20'
                              + 'user%2FCondition.read%20'
                              + 'user%2FDevice.read%20'
                              + 'user%2FGoal.read%20'
                              + 'user%2FImmunization.read%20'
                              + 'user%2FMedication.read%20'
                              + 'user%2FMedicationStatement.read%20'
                              + 'user%2FPatient.read%20'
                              + 'user%2FPractitioner.read%20'
                              + 'user%2FProcedure.read%20'
                              + 'user%2FOrganization.read%20'
                              + 'user%2FObservation.read'
      }

      if(serviceName === "CernerProvider"){
        console.log('Detected the CernerProvider service...')
        loginUrl = loginUrl + '&aud=' + config.baseUrl 
                            + '&launch=' + Session.get('ehrLaunchContext')
                            + '&scope=user%2FPatient.read%20'
                              + 'user%2FAllergyIntolerance.read%20'
                              + 'user%2FCarePlan.read%20'
                              + 'user%2FCondition.read%20'
                              + 'user%2FDevice.read%20'
                              + 'user%2FGoal.read%20'
                              + 'user%2FImmunization.read%20'
                              + 'user%2FMedication.read%20'
                              + 'user%2FMedicationStatement.read%20'
                              + 'user%2FPatient.read%20'
                              + 'user%2FPractitioner.read%20'
                              + 'user%2FProcedure.read%20'
                              + 'user%2FOrganization.read%20'
                              + 'user%2FObservation.read'
      }

      if(serviceName === "Allscripts"){
        console.log('Detected the AllScripts service...')
        loginUrl = loginUrl + '&scope=launch%20openid%20profile%20patient%2F*.read%20'
                            + '&acr_values=FhirApiID:6dc1c200-b14f-407f-9d98-4ea8c3f5627f'
      }

      if(serviceName === "Touchworks"){
        console.log('Detected the AllScripts Touchworks service...')
        loginUrl = loginUrl + '&scope=launch%20openid%20profile%20patient%2F*.read%20'
                            + '&acr_values=FhirApiID:e9a5eab3-b766-4b86-a0d2-8634311fce2c'
      }

      if(['DynamicFhir', 'DynamicHealth'].includes(serviceName)){
        console.log('Detected the DynamicFhir service...')
        loginUrl = loginUrl + '&scope=offline_access%20FHIRApi%20openid%20profile%20patient%2F*.read'
                              // probably want to remove this in the future and change the secret
                              // but for now, policy is that we don't support secrets
                              // so, we just hardcode itÃŸ
                              // DynamicHealth

                              // might need to urlEncode the redirect
                              // + '&grant_type=authorization_code'
                            + '&client_secret=3B79E5D274954B7EA97C';
      }

      if(get(Meteor, 'settings.public.logging') === "debug"){
        console.log('OAuth.requestCredential().loginUrl', loginUrl);
      }  
    
      var loginOptions = {
        loginService: serviceName,
        loginStyle: loginStyle,
        loginUrl: loginUrl,
        credentialRequestCompleteCallback: credentialRequestCompleteCallback,
        credentialToken: credentialToken,
        popupOptions: {
          height: 600,
          width: 480
        }
      }
    
      if(get(Meteor, 'settings.public.logging') === "debug"){
        console.log('OAuth.requestCredential().loginOptions', loginOptions);
      }  
      
      OAuth.launchLogin(loginOptions);
  }
};
