  fetchAccessToken(){
    console.log('C11.1   Fetching access token')
    var self = this;
    Meteor.call('fetchAccessToken', Session.get('oauthService'), function(err, result){
        if(result){
            console.log('C11.2   Received access token.', result)
            var newState = self.state;
            newState.accessToken = result.accessToken;
            if(result.accessToken){ 
                self.setState(newState)
                Session.set('accessToken', result.accessToken)
                self.fetchIdentity();    
            } else {
                console.log('No access token found.  Check implementation.')
            }
        }
    })
  }
  fetchIdentity(){
    console.log('C12.1   Fetching patient OAuth identifier.')
    var self = this;
    Meteor.call('fetchIdentity', Session.get('oauthService'), function(err, result){
        if(result){
            console.log('C12.1   Received patient OAuth identifier.', result)
            if(result.patient){

                var newState = self.state;
                newState.patientId = result.patient;
                self.setState(newState)

                self.fetchPatientData();    
            }
        }
    })      
  }
  fetchPatientData(){
    console.log('C13.0 this.state', this.state)
    var fetchUrl = Session.get('oauthBaseUrl') + '/Patient/' + this.state.patientId + formatSuffix;
    console.log('C13.1   Constructing patient data fetch url', fetchUrl)
    console.log('C13.2   Fetching patient data.')

    var self = this    

    HTTP.get(fetchUrl, {
        // data: {
        //   summaryLength: "500",
        //   entryPoint: "main"
        // },
        headers: {
            'Authorization': 'Bearer ' + Session.get('accessToken')
        }
      }, function(err, result){
        if(result){
            console.log('C13.3   Received patient data.',  result)

            var patientData = {};
            if(result.content){
                patientData = JSON.parse(result.content);
                console.log('C13.4   Received patient data.',   patientData)    
            }

            var newState = self.state;
            newState.patientData = EJSON.stringify(patientData, {indent: 2});
            newState.patient = patientData;

            self.setState(newState);
        }
        if(err){
            console.error(err)
        }
    })
  }
  savePatientDemographicsToProfile(){
    console.log('savePatientDemographicsToProfile', this.state );

    console.log('Meteor.userId()', Meteor.userId());

    
    if(get(this, 'state.patient.name[0]')){
        var humanName = get(this, 'state.patient.name[0]');
        if (!humanName.text ){
            humanName.text = humanName.given[0] + '' + humanName.family[0];
        }
        Meteor.users.update({_id: Meteor.userId()}, {
            $set: {
                'profile.name': humanName
            }
        })    
    }
    if(get(this, 'state.patient.gender')){
        var gender = get(this, 'state.patient.gender');
        switch (gender) {
            case 'male':          
                Session.get('gender', 'BabyBlue')          
                break;
            case 'female':      
                Session.get('gender', 'Pink')          
                break;
            default:
                break;
        }
    }
    if(get(this, 'state.patient')){

        var newPatient = this.state.patient;

        // STU3 HumanName mapping
        var newName = get(newPatient, 'name[0]');
        newName.family = newName.family[0];
        newPatient.name = [ newName ]
        console.log('newPatient', newPatient);

        if(!Patiens.findOne({id: this.state.patient.id})){
            Patients.insert(newPatient, function(error){
                if(error) console.log('Patient.insert().error', error)
            });    
        }
    }
    if(get(this, 'state.patient.address')){
        var addresses = get(this, 'state.patient.address');
        addresses.forEach(function(address){
            if(address.use === "home"){
                Meteor.users.update({_id: Meteor.userId()}, {
                    $set: {
                        'profile.locations.home.address': address
                    }
                })            
            }
        })

    }
    
  }
