  clearLocalCache(){
    if(confirm("Are you absolutely sure?")){

      var resourceTypes = [
        'AllergyIntolerances',
        'CarePlans',
        'Conditions',
        'Devices',
        'DiagnosticReports',
        'Goals',
        'Immunizations',
        'Medications',
        'MedicationOrders',
        'MedicationStatements',
        'Organizations',
        'Observations',
        'Practitioners',
        'Procedures',
        'RiskAssessments'
      ];

      resourceTypes.forEach(function(resourceType){
        if(Mongo.Collection.get(resourceType)){
          Mongo.Collection.get(resourceType).find().forEach(function(record){
            Mongo.Collection.get(resourceType)._collection.remove({_id: record._id})
          })
        }
      })
    }
  }
  searchChart(){
    console.log('Asking server to fetch patient chart for us...')

    Meteor.call('fetchPatientChart', 
        Session.get('oauthBaseUrl'), 
        Session.get('accessToken'), 
        Session.get('smartOnFhirIdentity'), 
        Session.get('oauthService'), 
        function(error, result){
        if(error){
            console.error('searchChart.error', error)
        }
        if(result){
            console.info('searchChart.result', result)
        }
    })
  }
  cacheObservations(){
    console.log('cacheObservations', this.state.observations);
    
    if(this.state.observations.length > 0){
        this.state.observations.forEach(function(observation){
            Observations._collection.insert(observation, function(error){
                if(error) console.error(error)
            });
        })
    }
  }
