var getTokenResponse = function(query, config, autoscannedUrl) {
    if(get(Meteor, 'settings.public.logging') === "debug"){
        console.log('S10.1 getTokenResponse()', query, config, autoscannedUrl);
    }  

    var responseContent;
    var oauthTokenUrl;
    var httpResult;
    var accessToken;
    var expiresIn;
    var getTokenResponseResults = {
        accessToken: '',
        expiresIn: '',
        patient: ''
    }

    var tokenUrl = '/oauth/token';
    if (autoscannedUrl) {
      tokenUrl = autoscannedUrl;
    }

    var params = {
        grant_type: 'authorization_code',
        code: query.code,
        client_id: config.clientId,
        client_secret: OAuth.openSecret(config.secret),
        redirect_uri: OAuth._redirectUri(config.service, config)
    }
    process.env.DEBUG && console.log('S10.2 params', params);

    oauthTokenUrl = config.baseUrl + tokenUrl;
    if (config.tokenUrl){
        oauthTokenUrl = config.tokenUrl;
    } else {
        oauthTokenUrl = 'https://open-ic.epic.com/argonaut/oauth2/token';
    }
    if(get(Meteor, 'settings.public.logging') === "debug"){
        console.log('S10.3 Token Url: ', oauthTokenUrl);
    }  

    let message = { 
        params: params,
        // content: btoa('SymptomaticPHR2019:3B79E5D274954B7EA97C'),
        npmRequestOptions: {
            oauth: {
                body_hash: btoa('SymptomaticPHR2019:3B79E5D274954B7EA97C')
                // consumer_secret: '3B79E5D274954B7EA97C',
                // token_secret: '3B79E5D274954B7EA97C'
            }
        }
    }

    console.log('---------------------------------------------------------------')
    console.log('S10.3.1 Token Payload', message)
    console.log('---------------------------------------------------------------')

    httpResult = HTTP.post(oauthTokenUrl, message);

    // httpResult = HTTP.post(oauthTokenUrl, message, function(error, result){
    //     if(error){
    //         console.log('S10.3.error', error)
    //     }
    //     if(result){
    //         console.log('S10.3.result', result)
    //     }
    // });

    // HTTP.post(oauthTokenUrl, { params: params }, function(error, httpResult){
    //     if(error) {
    //         console.log('error in getTokenResponse()', error)
    //     }
    //     if(httpResult) {
    //         console.log('httpResult', httpResult);
    //         console.log('httpResult.data', httpResult.data);

    //         if(httpResult.data){
    //             getTokenResponseResults.accessToken = httpResult.data.access_token;
    //             getTokenResponseResults.expiresIn = httpResult.data.expiresIn;
    //         }
        
    //         console.log('getTokenResponseResults', getTokenResponseResults)
        
    //     }
    // });

    if(httpResult && httpResult.data){
        process.env.DEBUG && console.log('S10.4 httpResult');
        process.env.DEBUG && console.log('S10.5 httpResult.data', httpResult);

        getTokenResponseResults.accessToken = httpResult.data.access_token;

        if(get(httpResult, 'data.expiresIn')){
            getTokenResponseResults.expiresIn = get(httpResult, 'data.expiresIn');
        }
        if(get(httpResult, 'data.expires_in')){
            getTokenResponseResults.expiresIn = get(httpResult, 'data.expires_in');
        }

        if(get(httpResult, 'data.patient')){
            getTokenResponseResults.patient = get(httpResult, 'data.patient');
        }

        process.env.DEBUG && console.log('S10.6 getTokenResponseResults', getTokenResponseResults)
    } else {
        process.env.DEBUG && console.log('S10.4 httpResult.data does not exist?')
    }

    return getTokenResponseResults;
};

var getIdentity = function(accessToken, config) {
    var fetchUrl = config.baseUrl + '/oauth/getIdentity';
    return HTTP.get(fetchUrl, { params: {
      access_token: accessToken
    }}, function(error, httpResult){
        if(error) {
            // console.log('getIdentity().error', error)
        }
        if(httpResult) {
            process.env.DEBUG && console.log('S20. getIdentity().httpResult', httpResult);
            process.env.DEBUG && console.log('S20. getIdentity().httpResult.data', httpResult.data);
        }
    });
};


