  signInWith(serviceName){
    console.log('*********************************************')
    console.log('C1.1    Signing in with the following OAuth service:  ', serviceName)

    this.resyncConfiguration();

    var self = this;

    Session.set('oauthService', serviceName);

    var services = ServiceConfiguration.configurations.find().fetch();
    console.log('C1.2    Checking the existing service configurations in database: ', services);

    services.forEach(function(service){
        if(service.service === serviceName){
            console.log('C1.3.1  Found the service configuration', service)

            console.log('C1.3.2  Setting OAuth baseUrl...', service.baseUrl)

            Session.set('oauthBaseUrl', service.baseUrl);
        }
    })

    var options = {
        requestPermissions: [
            'OBSERVATION.READ', 
            'OBSERVATION.SEARCH', 
            'PATIENT.READ', 
            'PATIENT.SEARCH', 
            'PRACTITIONER.READ', 
            'PRACTITIONER.SEARCH',
            'patient/*.read',
            'patient/*.search',
            'openid',
            'profile',
            'user/*.*',
            'launch',
            'online_access'            
        ]
    }

    console.log('C1.3    Checking service names registered in active server memory', Accounts.oauth.serviceNames());

    var loginSignature = 'loginWith' + serviceName;
    console.log('C1.4    Building custom login signature required by accounts-oauth: ', loginSignature);

    options.serviceName = serviceName;
    console.log('C1.5    Building signin options (including custom scope requests): ', options);

    console.log('C1.6    Calling custom login signature.');
    Meteor[loginSignature].call(options, function(error, result){
        console.log('C1.7.1  Received a response from our custom login signature.')
        if(error){
            console.log('C1.7.2.  error', error)
        }
        if(result){
            console.log('C1.7.2.  result', result)
        }
        
        self.fetchAccessToken(function(){
            self.savePatientDemographicsToProfile(function(){
                self.searchChart();    
            });
        });

        console.log('C1.7.3. Completed running custom login signature callback.');         
    });
  }
  resyncConfiguration(){
    console.log('C2.1    Resyncing Configuration')
    console.log('C2.2    Fetching service names in active server memory', Accounts.oauth.serviceNames());
    var services = ServiceConfiguration.configurations.find().fetch();
    console.log('C2.3    Fetching service configurations currently stored in the database', services);

    services.forEach(function(record){
        Meteor['loginWith' + record.service] = function (options, callback) {

            console.log('C2.4    loginWith' + record.service);

            if (!callback && typeof options === "function") {
              callback = options;
              options = {};

              if(get(this, 'props.location.query.launch')){
                  options.launch = get(this, 'props.location.query.launch');
              }
            }

            if (_.isUndefined(OAuth2)) {
              OAuth2 = {
                  serviceName: ''
              };
            }

            OAuth2.serviceName = record.service;        
            console.log('C2.5    loginWith.OAuth2', OAuth2)
        
            var credentialRequestCompleteCallback = Accounts.oauth.credentialRequestCompleteHandler(callback);

            OAuth.requestCredential(options, credentialRequestCompleteCallback);                  
          };
    })

    return Meteor.call('resyncConfiguration');
  }
